// components/sections/Contact.tsx
'use client';
import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useTheme } from '@/contexts/ThemeContext';
import { useLanguage } from '@/contexts/LanguageContext';
import { useContact } from '@/hooks/useContact';
import { 
  FaWhatsapp, 
  FaEnvelope, 
  FaPaperPlane,
  FaGlobeAmericas,
  FaCheck,
  FaExclamationTriangle
} from 'react-icons/fa';

export default function Contact() {
  const { theme } = useTheme();
  const { language } = useLanguage();
  const { isSubmitting, submitStatus, submitContact } = useContact();
  
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  });

  const subjectOptions = [
    { value: 'web-development', label: language === 'pt' ? '🚀 Desenvolvimento Web' : '🚀 Web Development' },
    { value: 'backend', label: language === 'pt' ? '⚙️ Backend & APIs' : '⚙️ Backend & APIs' },
    { value: 'automation', label: language === 'pt' ? '🤖 Automação' : '🤖 Automation' },
    { value: 'other', label: language === 'pt' ? '✨ Outros' : '✨ Other' }
  ];

  // ... (restante do código igual)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // Aplica formatação final antes do envio
    const formattedData = {
      ...formData,
      name: formatName(formData.name),
      email: formatEmail(formData.email)
    };
    
    // Usa o hook para enviar
    await submitContact(formattedData);
    
    // Se foi sucesso, reseta o form
    if (submitStatus === 'success') {
      setFormData({
        name: '',
        email: '',
        subject: '',
        message: ''
      });
      
      // Reseta o status após 3 segundos
      setTimeout(() => {
        // Você pode adicionar uma função para resetar o status se quiser
      }, 3000);
    }
  };

  // ... (restante do código igual, mas adiciona feedback de status)

  return (
    <>
      <section id="contact" className="py-16 lg:py-20 transition-all duration-300 relative overflow-hidden">
        {/* ... */}
        
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-16 max-w-7xl mx-auto">
          {/* Contact Information - Left Side (igual) */}
          
          {/* Contact Form - Right Side */}
          <motion.div className={`p-8 rounded-3xl border-2 backdrop-blur-sm ${
            theme === 'dark'
              ? 'bg-gray-800/30 border-gray-700'
              : 'bg-white/50 border-gray-200'
          } shadow-2xl`}>
            <h3 className={`text-3xl font-bold mb-2 ${
              theme === 'dark' ? 'text-white' : 'text-gray-900'
            }`}>
              {language === 'pt' ? '📨 Envie sua Mensagem' : '📨 Send Your Message'}
            </h3>
            
            {/* Feedback de status */}
            {submitStatus === 'success' && (
              <motion.div
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                className="mb-6 p-4 rounded-xl bg-green-500/20 border border-green-500/30"
              >
                <div className="flex items-center space-x-2 text-green-600">
                  <FaCheck className="w-5 h-5" />
                  <span className="font-medium">
                    {language === 'pt' 
                      ? '✅ Mensagem enviada com sucesso! Entrarei em contato em breve.' 
                      : '✅ Message sent successfully! I\'ll get back to you soon.'
                    }
                  </span>
                </div>
              </motion.div>
            )}

            {submitStatus === 'error' && (
              <motion.div
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                className="mb-6 p-4 rounded-xl bg-red-500/20 border border-red-500/30"
              >
                <div className="flex items-center space-x-2 text-red-600">
                  <FaExclamationTriangle className="w-5 h-5" />
                  <span className="font-medium">
                    {language === 'pt' 
                      ? '❌ Erro ao enviar mensagem. Tente novamente.' 
                      : '❌ Error sending message. Please try again.'
                    }
                  </span>
                </div>
              </motion.div>
            )}

            <form onSubmit={handleSubmit} className="space-y-6">
              {/* ... (seus campos de formulário aqui) ... */}
            </form>
          </motion.div>
        </div>
      </section>
    </>
  );
}